const n = Math.floor(Math.random() * Math.floor(200))

const Card = (props) => (
  <div id={props.handPosition}>
    <div className="topLIneContainer">
      <p>Card Title</p>
      <p>Mana Cost</p>
    </div>
    <div className="artworkContainer">
      <img className="artwork" src="https://www.fillmurray.com/g/200/{n}"/>
    </div>
  </div>
)

export default Card



